*::before, *::after, * button, * {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}
*::before, *::after, * button {
  box-sizing: border-box;
}

button {
  width: 100%;
  border: none;
  outline: none;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100vw;
  height: 100vh;
  font-size: 10px;
  background: linear-gradient(90deg, #020024 10%, #1eab6e 49%, #020024 90%) no-repeat;
}

.calculator-grid {
  display: grid;
  max-width: 200px;
  width: 100%;
  height: 300px;
  grid-template: 2fr repeat(5, 1fr)/repeat(4, 1fr);
  gap: 4px 4px;
  overflow: hidden;
}

.span-4 {
  grid-column: span 4;
}

.span-2 {
  grid-column: span 2;
}

.output {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-direction: column;
}

.calculator-grid {
  position: relative;
  background-color: #050116;
  padding: 5px;
}

.button {
  position: relative;
  background-color: #110842;
  color: #1ef14c;
  cursor: pointer;
  user-select: none;
  transition: 300ms;
  z-index: 20;
  box-shadow: 0 0 80px 10px transparent;
}
.button:hover {
  background-color: #483a91;
  color: #75ee8f;
}
.button:active {
  transition: 50ms;
  background-color: #1e115e;
  transform: scale(0.8);
}

.button_active {
  display: block;
  position: absolute;
  background-color: #00ff4c;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  box-shadow: 0 0 10px 10px #00ff4c;
  z-index: 19;
  animation-name: shining;
  animation-duration: 500ms;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
}

@keyframes shining {
  10% {
    box-shadow: 0 0 10px 30px #00ff4c;
  }
  25% {
    box-shadow: 0 0 10px 50px #00ff4c;
  }
  50% {
    box-shadow: 0 0 10px 70px #00ff4c;
  }
  100% {
    box-shadow: 0 0 10px 110px transparent;
  }
}
.output {
  color: white;
  background-color: #050116;
  padding: 5px;
  row-gap: 0;
  border: 1px solid #1ef14c;
  z-index: 30;
}

.previous-operand {
  font-size: 1rem;
}

.current-operand {
  font-size: 1.5rem;
  line-height: 1rem;
}